function getRandomQuote () {
  let max = 10
  let min = 1
  let id = Math.floor(Math.random(Math.random()) * (max - min + 1)) + min
  return _quotes.find(q => q.id === id)
}

export default {
  getQuotes (cb) {
    setTimeout(() => cb(_quotes), 100)
  },

  getRacing (cb) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        let racing = {
          quote: getRandomQuote(),
          me: {
            name: 'Me',
            username: 'me',
            progress: 50,
            wpm: 0,
            wpmHistory: []
          }
        }
        resolve(racing)
      }, 2000)
    })
  }
}
